:root{
  --grey : #3E3E3E;
  --yellow : #FFD700;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #3E3E3E;
  color: var(--yellow);
  font-family: 'Playfair Display', serif;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root{
  height: 100%;
}
.head{
  background-color: black;
  height: 50px;
  box-shadow: 0 0 10px white;
  display: flex;
  align-items: center;
}
.head h1{
  margin-left: 20px;
}


/* -- */
.box{
  position: relative;
  width: 60%;
  height: 60%;
  background-color: rgb(0, 0, 0);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate( -50%, -50% );
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-shadow: 0 0 2px white;
  overflow-y: auto;
  overflow-x: hidden;
  
}
.box::-webkit-scrollbar {
  width: 9px;
}
/* Track */
::-webkit-scrollbar-track {
  background: var(--grey); 
}

/* Handle */
::-webkit-scrollbar-thumb {
border-radius: 10px;
  background: black; 
  box-shadow: inset 0 0 5px rgba(255, 255, 255, 0.459); 
  
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(32, 31, 31); 
  box-shadow:  0 0 5px black; 
  border-radius: 10px;
}
.box .head-box{
  width: 100%;
  height: 50px;
  background-color: var(--grey);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-top: 10px;
  box-shadow: 0 0 2px white;
  position: sticky;
  top: 0;
  z-index: 1;
}
.item{
  width: 100%;
  background-color: #191919;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 10px;
  font-size: 15px;
  font-weight: lighter;
  position: relative;
}

.edite,.delete{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--grey);
  padding: 10px;
  border-radius: 50px;
  box-shadow: 0 0 2px white;
  font-size: 13px;
  height: 10px;
  display: grid;
  align-content: center;
  text-align: center;
}
.edite{
  left: 0px;
  color: #28E841;
}
.delete{
  right: 0px;
  color: red;
  width: 10px;
}
.add{
  position: absolute;
  top: 400px;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--grey);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: grid;
  align-content: center;
  text-align: center;
  font-weight: bolder;
  font-size: 30px;
  box-shadow: 0 0 2px white;
}
.sure{
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
}
.over-box{
  background-color: var(--grey);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50% ,-50%);
  visibility: hidden;
  display: flex;
  transition: visibility 0s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.edite-box,.add-box{
  display: flex;
  flex-direction: column;
  align-content: center;
}
.edite-box button{
  background-color: var(--grey);
  padding: 10px;
  border-radius: 50px;
  box-shadow: 0 0 2px white;
  font-size: 13px;
  height: 10px;
  display: grid;
  align-content: center;
  text-align: center;
}
.inputs{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
}
.in-left,.in-right{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
}
@media only screen and (max-width : 768px) {
  .box{
    width: 90%;
    height: 70%;
  }
}